import createPersist from 'vuex-localstorage'

/**
 * 
 */

export default ({ name, version }) => {
  // 
  return {
    // Vuex  plugins
    plugins: [createPersist({
      //  name  version  key
      // 
      namespace: `${name}@${version}`,
      expires: 7 * 24 * 60 * 60 * 1e3
    })]
  }
}



